import React, {useEffect, useState} from "react";
import Curriculum from "../components/cv/cv.component.js";
import SEO from "../components/seo";
import Layout from "../components/layout";
import cv from '../../docs/cv-t.json';

const SecondPage = () => {
    const [extended, setExtended] = useState(false);

    useEffect(() => {
        setExtended(
            document.location.toString().includes('extended')
        );
    }, []);

    return (
        <Layout>
            <SEO title="Curriculum" />
            <Curriculum data={cv} extended={extended}/>

        </Layout>
    );
};

export default SecondPage
