import React from "react";
import {CvProjectCard, CvProjectCardAnimated} from "./cv-project-card";

const createBatchOf = (list, amount = 3) => {
    const batch = [];
    for (let idx = 0; idx < list.length; idx+=amount) {
        batch.push(list.slice(idx, idx+amount))
    }
    return batch;
}

export const CvProjectList = ({items, extended, animated = false}) => {
    if (animated) {
        const cards = items.map(project => <CvProjectCardAnimated project={project} />);
        const cardsBatches = createBatchOf(cards);
        return cardsBatches.map(batch => (
            <div className="columns">
                {batch.map(item => (
                    <div className="column is-4">{item}</div>
                ))}
            </div>
        ));
    }
    return items.map(project => <CvProjectCard project={project} key={project.name} extended={extended}/>)
}
