import React from "react";
import Project from "../project/project";

const Stack = ({items}) => items.map((tech) => <span key={tech} className="tag is-info is-light" >{tech}</span>);

export const CvProjectCard = ({project, extended}) => (
    <Project key={project.name} name={project.name} client={project.client} hidden={!extended}>
        {project.description.map(content => <p>{content}</p>)}
        <div className="tags">
            <span className="tag is-info">Tech Stack:</span>
            <Stack items={project.stack} />
        </div>
    </Project>
);

export const CvProjectCardAnimated = ({project}) => (
    <div className="card">
        <div className="card-content">
            <div className="media is-clipped" style={{height: 65}}>
                <div className="media-left is-marginless">
                    <figure className="image is-48x48" style={{margin: '0 1rem 0 0'}}>
                        <img src="https://bulma.io/images/placeholders/48x48.png" alt="Placeholder image"/>
                    </figure>
                </div>
                <div className="media-content">
                    <p className="title is-5">{project.name}</p>
                    <p className="subtitle is-6">{project.client}</p>
                </div>
            </div>

            <div className="content is-clipped" style={{height: 200}}>
                {project.description.map(content => <p>{content}</p>)}
            </div>
        </div>
        <footer className="card-footer is-clipped" style={{height: 130}}>
            <div className="box is-shadowless">
                <div className="tags">
                    <Stack items={project.stack} />
                </div>
            </div>
        </footer>
    </div>
);

