import React from 'react';

const IsHidden = ({condition, children}) => {
    if (condition) {
        return null;
    }

    return children;
};

const Project = ({ name = 'Project name', client="amazon", hidden=true, children }) => (
    <IsHidden condition={hidden}>
      <div className="card">

        <header className="card-header sticky has-background-white">
          <div className="media" style={{margin: '1rem 0'}}>
            <div className="media-left">
              <figure className="image is-48x48 is-hidden-touch">
                <img src="https://bulma.io/images/placeholders/96x96.png" alt="Placeholder image"/>
              </figure>
            </div>
            <div className="media-content">
              <p className="title is-4">{name}</p>
              <p className="subtitle is-6 has-text-grey">{client}</p>
            </div>
          </div>
        </header>

        <div className="card-content">
          <div className="content">
            {children}
          </div>
        </div>
        <footer className="card-footer">
        </footer>
      </div>
    </IsHidden>
);

export default Project;
