import React from 'react';
import classnames from 'classnames';

const Section = ({ name = 'experience', children, hideTitle = false }) => {
    const titleClassNames = classnames('title is-2', {'is-invisible': hideTitle})
    return (
        <section className="section">
            <div className="content">
                <h2 className={titleClassNames}>{name}</h2>
                {children}
            </div>
        </section>
    )
};

export default Section;
