import React, {useState} from "react";
import classnames from 'classnames';
import Section from '../section/section';
import Company from '../company/company.component';
import ProjectsContainer from '../projects/projects';
import Me from "../../../assets/me-and-kishka.jpg";
import {useSummaryVisibility} from "../../hooks/use-summary-visibility";
import {CvProjectList} from "./cv-project-list";

const Data = ({ data, extended }) => {
    const [isTitleHidden, setIsTitleVisible] = useState(false);
    const [isAnimated, setAnimated] = useState(false);
    const menuTitleClasses = classnames('menu-label small-title', {'is-invisible': !isTitleHidden});

    useSummaryVisibility((isHide) => {
        setIsTitleVisible(isHide);
    });

    return (
        <>
            <section className="section cv-summary">
                <div className="columns">
                    <div className="column is-one-quarter is-hidden-touch">
                        <div className="media">
                            <div className="media-content">
                                <div className="has-text-centered">
                                    <figure className="image is-128x128 is-inline-block">
                                        <img className="is-rounded" src={Me} />
                                    </figure>
                                </div>

                                <article className="media has-text-centered">
                                    <div className="media-left">
                          <span className="icon has-text-info">
                            <i className="ion-ios-mail" />
                          </span>
                                    </div>
                                    <div className="media-content">
                          <span className="is-size-7">
                            <a className="is-italic" href={`mailto:${data.email}`}>{data.email}</a>
                          </span>
                                    </div>
                                </article>

                            </div>
                        </div>
                    </div>

                    <div className="column">
                        <div className="content">
                            <div>
                                <h1 className="title">Hey there, I'm <span className="has-text-info">{data.name}</span></h1>
                                <h2 className="title is-5">Senior Software Engineer</h2>
                            </div>

                            <div className="content">
                                {data.about_me.map(paragraph => <p>{paragraph}</p>)}
                            </div>
                        </div>


                    </div>

                </div>
            </section>

            <div className="field has-addons">
                <p className="control">
                    <button className="button" onClick={() => setAnimated(false)}>
                            <span className="icon is-small">
                                <i className="fas fa-bold"></i>
                            </span>
                        <span>List</span>
                    </button>
                </p>
                <p className="control">
                    <button className="button" onClick={() => setAnimated(true)}>
                          <span className="icon is-small">
                            <i className="fas fa-italic"></i>
                          </span>
                        <span>Tiles</span>
                    </button>
                </p>
            </div>

            <Section name="Experience" hideTitle={isTitleHidden}>
                <div className="columns">
                    <div className="column is-3">
                        <aside className="menu sticky is-hidden-mobile">
                            <p className={menuTitleClasses}>
                                Experience
                            </p>
                            <ul className="menu-list">
                                {data.companies.map(company => {
                                    return (
                                        <>
                                            <li><a>{company.name}</a></li>
                                            <ul>
                                                {company.projects.map(project => <li><a>{project.name}</a></li>)}
                                            </ul>
                                        </>
                                    )
                                })}
                            </ul>
                        </aside>
                    </div>
                    <div className="column">
                        <ul className="is-marginless experience-list">
                            {data.companies.map(company => {
                                return (
                                    <Company name={company.name} description={company.description} jobPosition={company.role} timeFrame={company.period}>
                                    <ProjectsContainer>
                                        <CvProjectList items={company.projects} extended={extended} animated={isAnimated}/>
                                    </ProjectsContainer>
                                    </Company>
                                );
                            })}
                        </ul>
                    </div>
                </div>
          </Section>
        </>
    );
}

export default Data;
